import React, { Component } from 'react';
import './v2productgenerator.sass';
import TextareaAutosize from 'react-autosize-textarea';
import generateXML from './generateXML';


export default class V2ProductGenerator extends Component {
    state = {
        products: 1,
        prefix: undefined,
        xml: ''
    }
  generateXML = () => {
      this.setState({xml: generateXML(this.state.products, this.state.prefix)});
  };

  handleChange = (e) => {
      this.setState({[e.target.name]: e.target.value})
  }

  render() {
    return (
      <div className="container mt-4">
        <div className="row">
            <div className="col"></div>
          <div className="col">
            <div className="form-group">
              <label htmlFor="output-ids" className="font-weight-bold">
                  Number of products
              </label>
              <input className="form-control mb-2" type="number" value={this.state.products} name="products" onChange={this.handleChange} />
            </div>
            <div className="form-group">
              <label htmlFor="output-ids" className="font-weight-bold">
                 Prefix <sup>Optional</sup> 
              </label>
              <input className="form-control mb-2" value={this.state.prefix} placeholder="Product-"  name="prefix" onChange={this.handleChange}/>
            </div>
            <div className="form-group">
                <button className="btn btn-primary btn-block" onClick={this.generateXML}>Generate products</button>
                </div>
          </div>
          <div className="col"></div>
        </div>
        <div className="row mt-4 mb-4">
            <div className="col">
                <p className="font-weight-bold">Output</p>
                <TextareaAutosize className="form-control" value={this.state.xml}  name="xml" onChange={this.handleChange} readOnly />
            </div>
        </div>
      </div>
    );
  }
}
